<template>
  <div class="modifypwd">
    <!-- 更改手机 -->
    <div class="changetel"> <!--v-if="toggleStep === '1'"-->
      <div class="tit">
        <h2>更改手机</h2>
      </div>
      <p class="detail">请先确认当前的手机号+86 1823****252是否能接收短信，再选择修改方式：</p>
      <div class="change">
        <ul class="yzbd">
          <li>
            <div class="img"><img src="../../assets/images/changetel1.png" alt=""></div>
            <h2>原手机号能接收短信</h2>
            <div class="intro">
              <p>通过手机号接收短信验证的方式修改</p>
              <p>向原手机号 <span class="tel">+86 1824/****4568</span> </p>
              <p>发送短信验证码进行修改</p>
            </div>
            
            <input type="button" value="立即更改">
          </li>
          <li>
            <div class="img"><img src="../../assets/images/changetel2.png" alt=""></div>
            <h2>原手机号无法接收短信</h2>
            <div class="intro">
              <p>原手机号已丢失或停用</p>
              <p>请联系客服更改手机号</p>
            </div>
            
            <input type="button" value="在线客服">
          </li>
        </ul>
      </div>
    </div>
    <!-- 进行安全验证 -->
     <div class="changetel" style="display:none"> <!--v-if="toggleStep === '1'"-->
      <div class="tit">
        <div class="location">
          <router-link to=""> <strong>账号安全</strong> </router-link>>
          <router-link to="">邮箱验证</router-link>
        </div>
        <h2>更改手机</h2>
      </div>
      <div class="schedule">
        <ul class="wz">
          <li class="choose">进行安全验证</li>
          <li>设置新手机</li>
          <li>修改成功</li>
          <div class="clear"></div>
        </ul>
        <ul class="line">
          <li class="active"></li>
          <li></li>
          <li></li>
          <div class="clear"></div>
        </ul>
      </div>
      <form action="">
        <div class="change1">
          <ul class="yzbd">
            <li>
              <h4>原手机号</h4>
              <input
                type="text"
                placeholder="请输入原手机号"
                v-model="phoneNum"/>
            </li>
            <!-- <li>
              <h4>短信验证码</h4>
              <input
                type="text"
                class="yzm"
                placeholder="请输入短信验证码"
                v-model="imgVcode"/>
              <div class="yzdata" @click="changeImgVcode">
                <img :src="imgVcodeUrl"/>
              </div>
            </li> -->
            <li>
              <h4>短信验证码</h4>
              <div class="messageyz">
                <input 
                  type="text"
                  placeholder="请输入短信验证码"
                  v-model="vcode"/>
                <span @click="getVcode">获取验证码</span>
              </div>
            </li>
            <li>
              <input
                type="button"
                data-index='1'
                @click="next($event)"
                value="确定"/>
            </li>
          </ul>
        </div>
      </form>
    </div>

    <!-- 设置新密码 -->
     <div class="changetel" style="display:none"><!--v-if="toggleStep === '2'" -->
      <div class="tit">
        <div class="location">
          <router-link to=""> <strong>账号安全</strong> </router-link>>
          <router-link to="">邮箱验证</router-link>
        </div>
        <h2>更改手机</h2>
      </div>
      <div class="schedule">
        <ul class="wz">
          <li class="choose">进行安全验证</li>
          <li class="choose">设置新手机</li>
          <li>修改成功</li>
          <div class="clear"></div>
        </ul>
        <ul class="line">
          <li class="active"></li>
          <li class="active"></li>
          <li></li>
          <div class="clear"></div>
        </ul>
      </div>
      <form action="">
        <div class="change2">
          <ul class="yzbd">
            <li>
              <h4>新手机号</h4>
              <input
                type="text"
                placeholder="请输入新手机号"
                v-model="password"/>
            </li>
            <li>
              <h4>短信验证码</h4>
              <div class="messageyz">
                <input 
                  type="text"
                  placeholder="请输入短信验证码"
                  v-model="vcode"/>
                <span @click="getVcode">获取验证码</span>
              </div>
            </li>
            <li>
              <input
                type="button"
                data-index='2'
                @click="next($event)"
                value="确定"/>
            </li>
          </ul>
        </div>
      </form>
    </div>

    <!-- 修改成功 -->
   <div class="changetel" style="display:none">   <!-- v-if="toggleStep === '3'" -->
      <div class="tit">
        <div class="location">
          <router-link to=""> <strong>账号安全</strong> </router-link>>
          <router-link to="">邮箱验证</router-link>
        </div>
        <h2>更改手机</h2>
      </div>
      
      <div class="schedule">
        <ul class="wz">
          <li class="choose">进行安全验证</li>
          <li class="choose">设置新手机</li>
          <li class="choose">修改成功</li>
          <div class="clear"></div>
        </ul>
        <ul class="line">
          <li class="active"></li>
          <li class="active"></li>
          <li class="active"></li>
          <div class="clear"></div>
        </ul>
      </div>
      <form action="">
        <div class="change3">
          <div class="wb">
            <img src="../../assets/images/teltrue.png" alt="">
            <p>手机号修改成功~</p>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      toggleStep: '1',
      phoneNum: '18233582827',
      imgVcodeUrl: '',
      imgVcode: '',
      vcode: '',
      password: '',
      repassword: '',
      sid: ''
    }
  },
  created(){
    this.getImgVcode()
  },
  computed: {
    
  },
  methods: {
    // get imgCode
    getImgVcode(){
      this.$axios.post('/pcgc/getverify')
      .then((res) => {
        this.imgVcodeUrl = res.data.Verify_url
        this.sid = res.data.sid
      })
      .catch((err) => {})
    },

    // refresh imgCode
    changeImgVcode(){
      this.getImgVcode()
    },

    // next
    next(e){
      let index = e.toElement.dataset.index;
      switch (index) {

        // vertify
        case '1':
          this.$axios.post('/pcgc/request_yanzheng', {
            username: this.phoneNum,
            img_codes: this.imgVcode,
            id_code: this.vcode,
            sid: this.sid
          })
          .then((res) => {
            console.log(res)
            alert(res.data.msg)
            if(res.data.code === '1'){
              this.toggleStep = '2'
            }
          })
          .catch((err) => {})
          break;

        // reset pass
        case '2':
          this.$axios.post('/pcgc/resetpw', {
            newpassword: this.password,
            newrepassword: this.repassword
          })
          .then((res) => {
            alert(res.data.msg)
            if(res.data.code === '1') {
              this.toggleStep = '3'
            }
          })
          .catch((error) => {})
          break;
      }
    },

    // get phone vertification code
    getVcode(){
      this.$axios.post('/pcgc/getphone', {
        username: this.phoneNum
      })
      .then((res) => {
        console.log(res)
      })
      .catch((err) => {})
    }
  }
}
</script>
<style lang="scss" scoped>
.changetel{
  width:800px;
  height: 658px;
  background-color: #fff;
  margin:100px auto;
  box-shadow: 0px 2px 3px rgba(0,0,0,0.1);
  .tit{
    height: 90px;
    position: relative;
    border-bottom:1px solid #ebebeb;
    .location{
      top: 42%;
      left: 4%;
      position: absolute;
      a{
        font-size: 12px;
        color: #333;
      }
    }
    h2{
      top:50%;
      left: 50%;
      transform: translate(-50%,-50%);
      position: absolute;
      font-size: 28px;
      line-height:90px;
      font-weight:400;
      color: #2e2e2e;
      text-align: center;
    }
  }
  p.detail{
    margin:70px 0;
    color: #333;
    text-align: center;
    line-height:26px;
    font-size: 20px;
  }
  .change{
    .yzbd{
      width:600px;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      li{
        text-align: center;
        width:260px;
        height: 320px;
        border:1px solid #DEDEDE;
        .img{
          width:51px;
          height: 52px;
          margin:39px auto 35px auto;
          img{
            width:100%;
            height: 100%;
          }
        }
        h2{
          color: #333;
          font-size: 14px;
          margin-bottom:18px;
        }
        .intro{
          min-height: 57px;
          p{
            font-size: 14px;
            color: #999;
            line-height: 19px;
            .tel{
              font-size: 14px;
              color: #EA6457;
            }
          }
        }
        
        input[type="button"]{
          width:140px;
          height: 38px;
          margin-top:30px;
          background-color:#f1f1f1;
          border:1px solid #d9d9d9;
          border-radius: 5px; 
          font-size: #4d4d4d;
        }
      }
    }
  }
  .schedule{
    width:96%;
    margin: 70px auto 0 auto;
    font-weight: 400;
    ul.wz{
      font-size: 12px;
      color:#CFCFCF;
      width:100%;
      margin:0 auto;
      li{
        text-align: center;
        display: inline-block;
        &:nth-child(1){width:24%;}
        &:nth-child(2){width:50%;}
        &:nth-child(3){width:24%;}
        &.choose{color:#ea6457;}
      }
    }
    ul.line{
      color:#ea6457;
      width:80%;
      padding-left:10px;
      margin: 20px auto 0 auto;
      li{
        width:45%;
        height:1px;
        display: inline-block;
        background-color:#CFCFCF;
        position:relative; 
        margin-right:14px; 
        &:before{
          content:"";
          width:7px;
          height:7px;
          background-color:#CFCFCF;
          position:absolute;
          top:-4px;
          right:-12px;
          display:block;
          border-radius: 50%;
        }
        
        &:nth-child(1){
          width:0;
          margin:0;
          &:before{
            content:"";
            width:7px;
            height:7px;
            position:absolute;
            top:-4px;
            right:3px;
            display:block;
            background-color: #EA6457;
            border-radius: 50%;
          }
        }
      }
      .active{background-color: #EA6457;
        &:before{background-color: #EA6457;}
      }
    }
  }
  .change1{
    ul.yzbd{
      width: 390px;
      margin:35px auto 0 auto;
      li{
        width:100%;
        float: none;
        display: inline-block;
        margin-bottom: 20px;
        h4{
          font-size: 14px;
          color: #2e2e2e;
          font-weight: 400;
          line-height: 45px;
        }
        input[type="text"]{
          width:95%;
          height:40px;
          padding-left:15px;
          border:1px solid #cfcfcf;
          border-radius: 5px;
          font-size: 14px;
        }
        input[type="text"]:-input-placeholder{font-size: 14px;color: #cbcbcb;}
        input[type="text"]:-ms-input-placeholder{font-size: 14px;color: #CBCBCB;}
        input[type="text"]::-webkit-input-placeholder{font-size: 14px;color: #CBCBCB;}    /* 使用webkit内核的浏览器 */
        input[type="text"]:-moz-placeholder{font-size: 14px;color: #CBCBCB;}                  /* Firefox版本4-18 */
        input[type="text"]::-moz-placeholder{font-size: 14px;color: #CBCBCB;} /* Firefox版本19+ */
        input[type="button"]{width:390px;height: 40px;border-radius:5px;margin-top:40px;background-color: #EA6457;color: #fff;border: none;
        }                 
        input[type="text"].yzm{
          width:268px;
          height: 36px;
          vertical-align: top;
          display: inline-block;
        }
        .yzdata{
          width:100px;
          height:40px;
          display: inline-block;
          img{width:100%;height: 100%;}
        }
        .messageyz{
          position:relative;
          height: 44px;
          input[type="text"]{position: absolute;}
          span{
            width:90px;
            height: 30px;
            position:absolute;
            right:8px;
            top:7px;
            line-height:30px;
            text-align: center;
            background-color:#f5f5f5;
            display:inline-block;
            border-radius:5px;
            font-size:12px ;
          }
        }
      }
    }
  }
  .change2{
    ul.yzbd{
      width: 390px;
      margin:35px auto 0 auto;
      li{
        float: none;
        margin-bottom:20px;
        h4{
          font-size: 14px;
          color: #2e2e2e;
          font-weight: 400;
          line-height: 45px;
        }
        input[type="text"]{
          width:95%;
          height:40px;
          padding-left:15px;
          border:1px solid #cfcfcf;
          border-radius: 5px;
          font-size: 14px;
        }
        input[type="text"]:-input-placeholder{font-size: 14px;color: #cbcbcb;}
        input[type="text"]:-ms-input-placeholder{font-size: 14px;color: #CBCBCB;}
        input[type="text"]::-webkit-input-placeholder{font-size: 14px;color: #CBCBCB;}    /* 使用webkit内核的浏览器 */
        input[type="text"]:-moz-placeholder{font-size: 14px;color: #CBCBCB;}                  /* Firefox版本4-18 */
        input[type="text"]::-moz-placeholder{font-size: 14px;color: #CBCBCB;} /* Firefox版本19+ */
        input[type="button"]{
          width:390px;
          height: 40px;
          border-radius:5px;
          margin-top:40px;
          background-color: #EA6457;
          color: #fff;
          border: none;
        }
        .messageyz{
          position:relative;
          height: 44px;
          input[type="text"]{position: absolute;}
          span{
            width:90px;
            height: 30px;
            position:absolute;
            right:8px;
            top:7px;
            line-height:30px;
            text-align: center;
            background-color:#f5f5f5;
            display:inline-block;
            border-radius:5px;
            font-size:12px ;
          }
        }                 
      }
    }
  }
  .change3{
    .wb{
      width:80%;
      margin:60px auto 0 auto;
      text-align: center;
      p{
        color: #585858;
        text-align: center;
        float: none;
        margin:18px 0 30px 0;
        font-size: 18px;
        color:#585858;
        font-weight: 400;
        line-height: 28px;
      }
    }
  }
}
</style>



